.profile {
  margin: 0 auto;
  padding-top: 40px;
  width: 69%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width:1023px) {
  .profile {
    width: 88%;
    flex-direction: column;
    justify-content: center;
  }
}
