.header {
  margin: 0 auto;
  padding-top: 45px;
  min-height: 74px;
  width: 69%;
  display: flex;
  align-items: flex-start;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width:767px) {
  .header {
    width: 100%;
    min-height: 56px;
    padding-top: 28px;
  }
}